<template>
  <div class="wrap">
    <div class="form">
      <div class="item">
        <label for=""></label>
        <input type="text" v-model="form.name">
      </div>
      <div class="item">
        <label for=""></label>
        <input type="number" v-model="form.phone">
      </div>
      <div class="item">
        <label for=""></label>
        <input type="text" v-model="form.password">
      </div>
      <div class="item">
        <label for="">/</label>
        <input type="text" v-model="form.password">
      </div>
      <div class="item">
        <label for=""></label>
        <input type="text" v-model="form.password">
      </div>
      <div class="id-item" @click="selectFront">
        <div class="id">
          <img src="../assets/ID.svg" alt="">
          <p></p>
        </div>
        <div class="id-back">
          <img src="../assets/IDback.svg" alt="">
          <p></p>
        </div>
      </div>
    </div>
    <button @click="handleAction">
      
    </button>
  </div>
</template>
<script>
import wx from 'weixin-js-sdk';
// import { MessageBox } from 'mint-ui';
// import api from '../api/mgr';

export default {
  name: 'mgr-login',

  data() {
    return {
      form: {
        name: '',
        phone: '',
        password: '',
      },
    };
  },
  methods: {
    async handleAction() {
      this.$router.replace({
        name: 'Submit-success',
      });
      // const res = await api.login(this.form);
      // console.log(res);
    },
    selectFront() {
      wx.chooseImage({
        count: 9, // 9
        sizeType: ['original', 'compressed'], // 
        sourceType: ['album', 'camera'], // 
        success: res => {
          console.log('res', res);
          const localIds = res.localIds; // IDlocalIdimgsrc
          console.log('localIds', localIds);
          wx.uploadImage({
            localId: localIds[0], // IDchooseImage
            isShowProgressTips: 1, // 1
            success(res1) {
              const serverId = res1.serverId; // ID
              console.log('res1', res1);
              console.log('serverId', serverId);
            },
          });
        },
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.wrap {
  position: absolute;
  width: 100%;
  padding: 50px 30px 0;
  background: #f2f2f2;
}
.form {
  display: block;
  width: 100%;

  .item {
    display: block;
    margin-bottom: 10px;
    label {
      display: block;
      color: #666;
      margin-bottom: 3px;
    }
    input {
      display: block;
      width: 100%;
      border: 0;
      padding: 10px 5px;
      background: white;
      border-radius: 5px;
      outline-style: none;
    }
  }
}
.id-item {
  background: white;
  padding: 15px 12px;
  margin-top: 15px;
  border-radius: 5px;
  width: 100%;
  display: flex;
  & :nth-child(1) {
    margin-right: 20px;
  }
  img {
    width: 100%;
  }
  p {
    text-align: center;
    color: #666;
  }
}
button {
  width: 80%;
  margin: 50px 10%;
  background: #fdda06;
  padding: 15px;
  border: 0;
  border-radius: 5px;
  outline: none;
}
</style>





